@import url(https://fonts.googleapis.com/css?family=Hahmlet);
:root {
  /* CSS Colors from https://coolors.co/121212-14213d-2e4b8a-fca311-e5e5e5-ffffff*/
  --black-shadow: #121212ff;
  --blue-1: #14213dff;
  --blue-2: #2e4b8aff;
  --orange-accent: #fca311ff;
  --platinum: #e5e5e5ff;
  --white: #ffffffff;
  font-family: Hahmlet;
}

.button:hover {
  transition: 220ms all ease-in-out;
  background-color: var(--blue-2);
  color: var(--platinum);
  cursor: pointer;
}

.button:focus {
  outline-style: solid;
  outline-color: var(--orange-accent);
  border: solid 1px var(--orange-accent);
  background-color: var(--blue-2);
  color: var(--platinum);
}

.button {
  font-family: inherit;
  background-color: var(--white);
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.18);
  border: solid 3px var(--blue-2);
  border-radius: 10px;
  color: var(--blue-2);
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  padding: 0.4em 0.75em;
  height: 4em;
}

.buttonThin:hover {
  transition: 220ms all ease-in-out;
  background-color: var(--blue-2);
  color: var(--platinum);
  cursor: pointer;
}

.buttonThin:focus {
  outline-style: solid;
  outline-color: var(--orange-accent);
  border: solid 3px var(--orange-accent);
  background-color: var(--blue-2);
  color: var(--platinum);
}

.buttonThin {
  font-family: inherit;
  background-color: var(--white);
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.18);
  border: solid 3px var(--blue-2);
  border-radius: 8px;
  color: var(--blue-2);
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  padding: 0.2em 0.5em;
  height: 2.5em;
}

.buttonSolid:hover {
  border: solid 3px var(--blue-1);
  background-color: var(--blue-1);
  color: var(--platinum);
}

.buttonSolid {
  background-color: var(--blue-2);
  color: var(--white);
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--white);
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--blue-2);
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--orange-accent);
}

.loadingAnimation {
  display: none;
  height: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.loading {
  display: none;
}

.isloading .loadingAnimation {
  display: block;
}

.Home {
  text-align: center;
  background-image: url(/static/media/hero-image.56bf3f85.jpg);
  background-color: #cccccc; /* Used if the image is unavailable */
  height: 100vh; /* You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

.homeContainer {
  background-color: var(--platinum);
  box-shadow: 1px 1px 5px var(--black-shadow);
  border: 1px solid var(--black-shadow);
  border-radius: 10px;
  max-width: 700px;
  min-width: 500px;
  width: 50%;
  max-height: 300px;
  min-height: 200px;
  height: 25%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
}

.homeForm {
  padding: 0px 30px 0px 30px;
  max-width: 700px;
  min-width: 200px;
  width: calc(100% - 60px);
  grid-template-columns: 80px 1fr 1fr;
  grid-template-rows: 3em 3em;
  display: grid;
  align-items: center;
  grid-gap: 1em;
}

.inputText {
  background-color: var(--white);
  grid-column: 2/4;
  border-radius: 10px;
  height: 2em;
  font-size: 20px;
}

.showFilteredLink {
  grid-column: 2/3;
}

.showAllLink {
  grid-column: 3/4;
}

#label {
  font-size: 20px;
  margin: 15px;
}

#legend {
  font-weight: 900;
  font-size: 2em;
  margin-bottom: 5vh;
  margin-top: 1vh;
}

#heroImg {
  font-size: 1em;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  text-align: center;
  position: absolute;
  font-weight: light;
  color: var(--platinum);
  text-shadow: 2px 2px 4px var(--black-shadow);
}

#heroImg a:link,
#heroImg a:visited,
#heroImg a:hover,
#heroImg a:active {
  color: var(--platinum);
}

/* Reset CSS from https://www.codegrepper.com/code-examples/css/reset+css+2021 */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

